<template>
    <header class="masthead" style="background-image: url('/assets/img/wrench.jpg');">
        <div class="overlay"></div>
        <div class="container">
            <div class="row">
                <div class="col-md-10 col-lg-8 mx-auto">
                    <div class="post-heading">
                        <h1 v-text="titulo"></h1>
                    </div>
                </div>
            </div>
        </div>
    </header>
</template>
<script>
export default {
    props: ['admin'],
    computed: {
        titulo(){
            let mensaje = '';
            console.log(this.admin);
            if(this.admin == 'simple'){
                mensaje =  'Bienvenido a la página de Administración';
            }else if(this.admin == 'avanzado'){
                mensaje = 'Esta página de administración está en construcción. Intente como administrador simple';
            }else{
                mensaje = 'La ruta no existe'
            }
            return mensaje;
        }
    },
}
</script>